<template>
  <el-container>
    <el-main>
      <el-table
          ref="multipleTable"
          :data="ads"
          style="width: 100%"
          tooltip-effect="dark"
      >
        <el-table-column
            label="id"
            prop="id"
            width="180">
        </el-table-column>
        <el-table-column
            label=""
            prop="slogan"
            width="180">
        </el-table-column>
        <el-table-column
            label=""
            prop="url"
            width="180">
        </el-table-column>
        <el-table-column
            label=""
            width="180">
          <template slot-scope="scope">
            <el-image
                :preview-src-list="fileList"
                :src="ads[scope.$index].image"
                fit="cover"
                style="width: 200px; ">
            </el-image>
          </template>
        </el-table-column>
        <el-table-column
            label=""
            width="180">
          <template slot-scope="scope">
            {{ new Date(ads[scope.$index].createTime).toLocaleString() }}
          </template>
        </el-table-column>
        <el-table-column
            label=""
            prop="expire"
            width="180">
        </el-table-column>
        <el-table-column
            align="right">
          <template slot="header" slot-scope="scope">
            <el-button size="mini" type="primary" @click="addDialog=true"></el-button>
          </template>
          <template slot-scope="scope">
            <el-button
                size="mini"
                type="danger"
                @click="()=>{editDialog=true;ad=ads[scope.$index];image.url=ad.image}"
            >
            </el-button>
            <el-button
                size="mini"
                type="primary"
                @click="delAd(scope.$index)"
            >
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog
          :close-on-click-modal="false"
          :visible.sync="addDialog"
          title=""
          width="30%">
        <div>
          <el-form ref="form" :model="ad" label-position="left" label-width="80px">
            <el-form-item>
              <el-upload
                  :auto-upload="false"
                  :file-list="[image]"
                  :on-change="handleChange"
                  action="#"
                  class="upload-demo"
                  list-type="picture">
                <el-button size="small" type="primary"></el-button>
                <div slot="tip" class="el-upload__tip">jpg/png25MB</div>
              </el-upload>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="ad.slogan" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="ad.url" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="()">
              <el-slider
                  v-model="ad.expire"
                  show-input>
              </el-slider>
            </el-form-item>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
    <el-button @click="addDialog= false"> </el-button>
    <el-button type="primary" @click="add"> </el-button>
      </span>
      </el-dialog>
      <el-dialog
          :close-on-click-modal="false"
          :visible.sync="editDialog"
          title=""
          width="30%">
        <div>
          <el-form ref="form" :model="ad" label-position="left" label-width="80px">
            <el-form-item>
              <el-upload
                  :auto-upload="false"
                  :file-list="[image]"
                  :on-change="handleChange"
                  action="#"
                  class="upload-demo"
                  list-type="picture">
                <el-button size="small" type="primary"></el-button>
                <div slot="tip" class="el-upload__tip">jpg/png25MB</div>
              </el-upload>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="ad.slogan" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="ad.url" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="()">
              <el-slider
                  v-model="ad.expire"
                  show-input>
              </el-slider>
            </el-form-item>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
    <el-button @click="editDialog= false"> </el-button>
    <el-button type="primary" @click="updateAd"> </el-button>
      </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>

<script>
import {addAd, delAd, getAdList, updateAd} from "@/admin/api/Common";

export default {
  name: "AdvertisingManage",
  data() {
    return {
      ads: [],
      ad: {
        slogan: '',
        url: '',
        expire: 0
      },
      image: null,
      addDialog: false,
      fileList: [],
      editDialog: false
    }
  },
  methods: {
    getAd() {
      getAdList().then((res) => {
        this.ads = res.data.ads
        for (let i = 0; i < this.ads.length; i++) {
          this.fileList.push(this.ads[i].image)
        }
      })
    },
    add() {
      this.addDialog = false
      addAd(this.ad, this.image.raw).then((res) => {
        if (res.code === 2001) {
          this.ad = {}
          this.ads.push(res.data.ad)
          this.fileList.push(res.data.ad.image)
          this.addDialog = false;
          this.$notify({
            title: '',
            message: '' + res.data.ad.expire + '',
            type: 'success'
          });
        } else {
          this.$notify({
            title: '',
            message: '' + res.message,
            type: 'error'
          });
        }
      })
    },
    updateAd(index) {
      updateAd(this.ad, this.image.raw).then((res) => {
        if (res.code === 2001) {
          this.ad = {}
          this.ads.splice(index, 1, res.data.ad);
          this.fileList.push(res.data.ad.image)
          this.editDialog = false;
          this.$notify({
            title: '',
            message: '' + res.data.ad.expire + '',
            type: 'success'
          });
        } else {
          this.$notify({
            title: '',
            message: '' + res.message,
            type: 'error'
          });
        }
      })
    },
    delAd(index) {
      let id = this.ads[index].id;
      delAd(id).then((res) => {
        if (res.code === 2001) {
          this.ads.splice(index, 1);
          this.fileList.splice(index, 1);
          this.$notify({
            title: '',
            message: '',
            type: 'success'
          });
        } else {
          this.$notify({
            title: '',
            message: '' + res.message,
            type: 'error'
          });
        }
      })
    },
    handleChange(file, fileList) {
      this.image = file
    }
  },
  created() {
    this.getAd()
  }
}
</script>

<style scoped>
.el-main::-webkit-scrollbar {
  width: 0 !important
}
</style>