<template>
  <el-container>
    <el-container>
      <el-aside style="width: 15%;margin-left: 20px;overflow: visible">
        <el-tag v-for="tag in tags"
                :key="tag"
                :disable-transitions="false"
                class="article-tag"
                closable
                effect="dark"
                @close="handleClose(tag)">
          {{ tag }}
        </el-tag>
        <el-input
            v-if="inputVisible"
            ref="saveTagInput"
            v-model="inputValue"
            class="input-new-tag"
            size="small"
            @blur="handleInputConfirm"
            @keyup.enter.native="handleInputConfirm"
        >
        </el-input>
        <el-button v-else class="button-new-tag" size="small" style="position: absolute;margin-top: 10px"
                   @click="showInput">+ New Tag
        </el-button>
      </el-aside>
      <el-main>
        <div id="title">
          <el-input v-model="article.title" autosize maxlength="50" placeholder="" show-word-limit
                    style="width: 70%;float: left;font-size: 18px" type="textarea"></el-input>
          <el-button round style="float: right" type="primary" @click="postArticle"></el-button>
        </div>

        <div id="editor">
          <mavon-editor ref="md"
                        v-model="editor.value" style="width: 100%;margin-top: 30px" @imgAdd="imgAdd"/>
        </div>

      </el-main>
    </el-container>

  </el-container>
</template>

<script>
import Navigation from "@/components/Navigation";
import {getArticleDetail, postArticle, update} from "@/api/Article";
import {uploadFile} from "@/api/Common";

export default {
  name: "Write",

  data() {
    return {
      id: String,
      inputVisible: false,
      inputValue: '',
      article: {
        title: '',
        content: '',
        tags: [],
        description: ''
      },
      tags: [],
      editor: {
        value: '',
      },
      flag: 0
    }
  },
  methods: {
    handleClose(tag) {
      this.tags.splice(this.tags.indexOf(tag), 1);
    },

    showInput() {
      this.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },

    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.tags.push(inputValue);
      }
      this.inputVisible = false;
      this.inputValue = '';
    },
    postArticle() {
      this.article.content = this.$refs.md.d_value
      this.article.tags = this.tags
      console.log(this.article)
      if (this.id) {
        this.article.id = this.id
        update(JSON.stringify(this.article)).then((res) => {
          if (res.code == 2001) {
            this.$notify({
              title: "HitBytebin",
              message: res.message,
              type: "success",
              duration: 2000
            })
            this.flag = 1
            this.$router.push("/home")
          } else {
            this.$notify({
              title: "Bloggy",
              message: res.message,
              type: "error",
              duration: 1000
            })
          }
        })
      } else {
        postArticle(JSON.stringify(this.article)).then((res) => {
          if (res.code == 2001) {
            this.$notify({
              title: "Bloggy",
              message: res.message,
              type: "success",
              duration: 2000
            })
            this.flag = 1
            this.$router.push("/home")
          } else {
            this.$notify({
              title: "Bloggy",
              message: res.message,
              type: "error",
              duration: 1000
            })
          }
        })

      }
    },
    imgAdd(pos, file) {
// 
      let formData = new FormData()
      formData.append('image', file)
      uploadFile(formData).then((res) => {
        // .url![...](./0) -> ![...](url)
        /**
         * $vm mavonEditor
         * 1.  : `import {mavonEditor} from ...` `$vm``mavonEditor`
         * 2. $refs: htmlref : `<mavon-editor ref=md ></mavon-editor>`$vm` `this.$refs.md`
         *
         */
        this.$refs.md.$img2Url(pos, res.data.path)
      })
    },
    getDetail() {
      getArticleDetail(this.id).then((res) => {
        this.article = res.data.article
        this.tags = res.data.article.tags
        this.editor.value = this.article.content
      })
    }
  },
  components: {
    'navigation': Navigation
  },
  beforeDestroy() {
    let value = this.$refs.md.d_value
    if (this.flag == 0) {
      this.$cookie.set("draft", value, 24 * 60 * 60 * 30)
    } else {
      this.$cookie.delete("draft")
    }
  },
  created() {
    this.id = this.$route.query.id
    if (this.id) {
      this.getDetail()
    }
    return
    this.editor.value = this.$cookie.get("draft")
  }
}
</script>

<style scoped>
#editor {
  width: 80%;
  text-align: center;
  margin: 100px auto 20px;
}

#title {
  width: 80%;
  text-align: center;
  background-color: #42b983;
  margin: 0 auto 0;
}

li {
  list-style-type: none;
}

.el-tag {
  white-space: normal;
  height: auto;
  text-align: left;
  border-radius: 8px;
  float: left;
  margin-left: 10px;
  margin-top: 10px;
}
</style>
